.App {
  text-align: center;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.korova {
  position: fixed;
  right: 0;
  bottom: 0;
}

.btn {
  cursor: pointer;
  background-color: brown;
  border: none;
  color: white;
  border-radius: 15px;
  width: 100px;
  height: 30px;
  margin-top: 10px;
}

.info {
  display: flex;
  width: 100%;
  justify-content: space-around;
  border: 2px solid gray;
  margin: 20px 0;

}

.button {
  cursor: pointer;
  height: 30px;
  border: none;
  border-radius: 15px;
  background-color: rgb(36, 36, 36);
  margin: 0 10px;
  padding: 0 15px;
  color: white;
}

.select {
  border-radius: 0;
}

.link {
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  background-color: green;
  margin-top: 10px;
}

input {
  box-sizing: border-box;
  width: auto;
  height: auto;
  border-radius: 0;
}

.inputDate {
  width: 150px;
  color: white;
}
